  /* MENU BURGER*/

  .menu {
      position: relative;
      width: 30px;
      height: 30px;
      margin: 25px;
  }
  .menu span {
      margin: 0 auto;
      position: relative;
      top: 12px;
  }

  .menu span:before, .menu span:after {
      position: absolute;
      content: '';
  }
  .menu span, .menu span:before, .menu span:after {
      width: 30px;
      height: 6px;
      background-color: #000;
      display: block;
  }
  .menu span:before {
      margin-top: -12px;
  }
  .menu span:after {
      margin-top: 12px;
  }

  /*Fake-Trigger*/
  #sized {
    width: 100%;
        height: auto;
        color: transparent;
        background-color: transparent;
        border: transparent;
  }

  /*Modal-Box*/
  .modal {
      display: none;
      position: fixed;
      z-index: 1;
      /*padding:0.5%;*/
      left: 0;
      top: 0;
      width: 100%; 
      height: 100%;
      overflow: auto;
  }

  .modal-content {
      position: relative;
      margin: auto;
      padding: 0;
      width: 100%;
      height: 100%;
      box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
      -webkit-animation-name: animatetop;
      -webkit-animation-duration: 0.4s;
      animation-name: animatetop;
      animation-duration: 0.4s;
      background-color: #FFF;
  }

  .modal-header {
      padding: 2px 16px;
      background-color: #FFF;
      border-bottom: none;
      height: 10vh;
  }

  .modal-body {
      padding: 2px 16px;
      background-color: #FFF;
      margin-bottom: 20%;
  }

  .modal-footer {
      padding: 2px 16px;
      background-color: #FFF;
      color: black;
      border-top: none;
      text-align: center;
  }

  .modal-footer hr {
      /*margin-top: 10rem;*/

  }

  .modal-footer>p {
    color: #000
  }

  .modal-footer h3 {
      text-transform: none;
      font-size:4vh;

  }

  .close {
      color: #000;
      float: left;
      font-size: 5em;
      font-weight: bold;
      opacity: 1;
  }

  .close:hover,
  .close:focus {
      color: #000;
      text-decoration: none;
      cursor: pointer;
  }

  @-webkit-keyframes zoomIn {
      from {
          opacity: 0;
          -webkit-transform: scale3d(.2, .2, .2);
          transform: scale3d(.2, .2, .2);
      }

      50% {
          opacity: 1;
      }
  }

  .zoomIn {
      -webkit-animation-name: zoomIn;
      animation-name: zoomIn;
  }

  .animated {
      -webkit-animation-duration: .2s;
      animation-duration: .2s;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
  }


  /*Navigation Inner Styling*/

